* {
  box-sizing: border-box;
  outline: 0;
  margin: 0;
  padding: 0;
}

.play-btn {
  text-align: center;
  cursor: pointer;
  margin-top: 30px;
  font-size: 1.5rem;
}

.player {
  align-items: center;
  gap: 10px;
  max-width: 90%;
  margin: 0 auto;
  display: flex;
}

.progress-bar {
  cursor: pointer;
  background: gray;
  width: 100%;
  height: 10px;
  position: relative;
}

.progress-bar .progress {
  background: red;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.progress-bar .progress span {
  cursor: pointer;
  background: #8a2be2;
  border-radius: 50%;
  width: 13px;
  height: 13px;
  display: block;
  position: absolute;
  top: -1.5px;
  right: 0;
  translate: 50%;
}

.progress-bar .timer {
  color: #fff;
  background: #000;
  font-size: .5rem;
  display: none;
  position: absolute;
  top: -100%;
  left: 0;
  translate: -50%;
}

